___CSS_LOADER_EXPORT___.push([module.id, `body {
	font-family: Arial, sans-serif;
	margin: 0;
	display: flex;
	height: 100vh;
}

#sidebar {
	width: 25%;
	min-width: 120px;
	border-right: 1px solid #ddd;
	padding: 10px;
} 

#chat {
	width: 75%;
	padding: 10px;
	display: flex;
	flex-direction: column;
}

#messages {
	flex-grow: 1;
	overflow-y: auto;
	border: 1px solid #ddd;
	padding: 10px;
	margin-bottom: 10px;
}

.message {
	margin-bottom: 10px;
	display: flex;
	flex-direction: column;
}

.message.right {
	text-align: right;
}

.message-sender {
	color: #008ec7;
	font-weight: bold1;
}

#input-container {
	display: flex;
}

#message-input {
	flex-grow: 1;
	padding: 10px;
}

#send-button {
	padding: 10px;
}

.message-timestamp {
    font-size: 0.8em;
    color: gray;
    margin-top:5px;
}

.message-content {
	padding: 10px;
}

#user-list {
    list-style: none; 
    padding-left: 25px; 
}

#user-list li {
    display: flex; 
    align-items: center;
    position: relative;
    padding: 5px 10px; 
	margin-left: 10px;
	margin-bottom: 30px;
}

#user-list li::before {
    content: "";
    display: inline-block;
    width: 50px; 
    height: 50px; 
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover; 
    background-repeat: no-repeat;
    position: absolute;
    left: -45px; 
    top: 50%; 
    transform: translateY(-50%); 
}
